<template>
  <div>
    {{responseResult}}
  </div>
</template>

<script>
  export default {
    name: 'Header',
    data () {
      return {
        nowuser:'',
        responseResult: ''
      }
    },
    mounted: function() {
      this.getUser();
    },
    methods: {
      getUser () {
        this.$axios
          .get('/getUser')
          .then(successResponse => {
            this.responseResult = JSON.stringify(successResponse.data)

          })
          .catch(failResponse => {})
      }
    }

  }
</script>



